import React, { useEffect, useState } from 'react';
import './Doctors.css';

import { Link } from 'react-router-dom';

import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import { createTheme, ThemeProvider } from '@mui/material/styles';
import { red } from '@mui/material/colors';

const Doctors = () => {
    
    const redTheme = createTheme(
        { 
            palette: { 
                primary: {
                    main: red[600],
                }
            } 
        }
    )

    const [ DoctorsList, setDoctorsList ] = useState([]);

    useEffect(
        () => {

            setDoctorsList(
                [
                    {
                        id: 1,
                        image: 'https://freesvg.org/img/Faceless-Male-Avatar-In-Suit.png',
                        name: 'Dr.Abdullah',
                        specialization: 'Andrologist, Endourologist, Sexologist, Urologist',
                        experience: 28,
                        reviews: 63
                    },
                    {
                        id: 2,
                        image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSbvEViMOriytRWM-Wu2DzCJAwGtVdEFx26RUmHxCb6XwZX_rCNvgQ4yYg7_2M_KeK74vQ&usqp=CAU',
                        name: 'Dr.Fawad',
                        specialization: 'Andrologist, Sexologist, Urologist',
                        experience: 14,
                        reviews: 55
                    },
                    {
                        id: 3,
                        image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSla-p7MqWVSLL2rpSQHlxEO6mKceKYPvZjo4oslefoeXE7-oMcRHP5IfT3qgllHC8kKvQ&usqp=CAU',
                        name: 'Dr.Yousuf',
                        specialization: 'Endourologist, Sexologist, Urologist',
                        experience: 45,
                        reviews: 88
                    }
                ]
            )

        }, []
    )

    return (
        <>
            <div className="doctors_container">

                <div className="doctors_container_content">
                    
                    {
                        DoctorsList.map(
                            val => {

                                return (
                                    <Doctor 
                                        data={ val } 
                                        key={ val.id }
                                        redTheme={ redTheme }
                                    />
                                )

                            }
                        )
                    }

                </div>

            </div>
        </>
    )

}

export default Doctors;

const Doctor = ( props ) => {

    return (
        <div className="doctor">

            <div className="d-flex justify-content-center">
                <Avatar alt="Doctor" src={ props.data.image } sx={{ width: 150, height: 150 }} />
            </div>
            <div className="d-flex justify-content-between w-100">

                <div>
                    <Typography variant="h5" component="div" gutterBottom>
                        { props.data.name }
                    </Typography>

                    <Typography variant="subtitle1" component="div">
                        { props.data.specialization }
                    </Typography>

                    <Typography variant="subtitle1" component="div">
                        { props.data.experience } year(s) Experience
                    </Typography>

                    <Typography variant="subtitle2" component="span">
                        { props.data.reviews } patient reviews
                    </Typography>
                </div>

                <div>
                    <Link to={ "/book_appointment/" + props.data.id }>
                        <Button variant="contained" color="primary" size="large">
                            Video Consultation
                        </Button>
                    </Link>

                    <br />
                    <br />
                    
                    <ThemeProvider theme={ props.redTheme }>
                        <Link to={ "/book_appointment/" + props.data.id }>
                            <Button variant="contained" color="primary" size="large">
                                Audio Consultation
                            </Button>
                        </Link>
                    </ThemeProvider>
                </div>

            </div>

        </div>
    )

}