import React, { useEffect, useState } from 'react';
import './VideoConsulting.css';

import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';

import PhoneIcon from '@mui/icons-material/Phone';

// import MicOffIcon from '@mui/icons-material/MicOff';
// import KeyboardVoiceIcon from '@mui/icons-material/KeyboardVoice';

// import VideocamOffIcon from '@mui/icons-material/VideocamOff';
// import VideocamIcon from '@mui/icons-material/Videocam';

import $ from 'jquery';
import socket from '../../io';
import { Peer } from "peerjs";

const VideoConsulting = () => {

    const [ Loaded, setLoaded ] = useState(false);
    const [ AppointmentList, setAppointmentList ] = useState([]);
    const [ CallTo, setCallTo ] = useState({});
    // const [ Calling, setCalling ] = useState(false);
    // FOR THE PREVIOUS STATE
    // const [ TurnVideoOff, setTurnVideoOff ] = useState(false);
    // const [ TurnAudioOff, setTurnAudioOff ] = useState(false);

    useEffect(
        () => {

            let arr = [
                {
                    id: 1,
                    doctor_name: 'Dr.Abdullah',
                    doctor_image: 'https://freesvg.org/img/Faceless-Male-Avatar-In-Suit.png',
                    doctor_gender: 'Male',
                    doctor_experience: 28,
                    appointment_time: '06:00 PM',
                    appointment_date: 'Thu 2 Feb 2022',
                    appointment_type: 'Video Consulting'
                },
                {
                    id: 2,
                    doctor_name: 'Dr.Fawad',
                    doctor_image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSbvEViMOriytRWM-Wu2DzCJAwGtVdEFx26RUmHxCb6XwZX_rCNvgQ4yYg7_2M_KeK74vQ&usqp=CAU',
                    doctor_gender: 'Male',
                    doctor_experience: 14,
                    appointment_time: '04:00 PM',
                    appointment_date: 'Mon 7 Feb 2022',
                    appointment_type: 'The Aga Khan University Hospital'
                },
                {
                    id: 3,
                    doctor_name: 'Dr.Shahzain',
                    doctor_image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSla-p7MqWVSLL2rpSQHlxEO6mKceKYPvZjo4oslefoeXE7-oMcRHP5IfT3qgllHC8kKvQ&usqp=CAU',
                    doctor_gender: 'Male',
                    doctor_experience: 10,
                    appointment_time: '01:00 PM',
                    appointment_date: 'Fri 6 Feb 2022',
                    appointment_type: 'Mamji Hospital'
                },
                {
                    id: 4,
                    doctor_name: 'Dr.Yousuf',
                    doctor_image: 'https://www.w3schools.com/w3images/avatar2.png',
                    doctor_gender: 'Male',
                    doctor_experience: 4,
                    appointment_time: '12:00 PM',
                    appointment_date: 'Sat 1 Feb 2022',
                    appointment_type: 'Memon Medical Institute Hospital'
                }
            ]
            setAppointmentList(
                arr
            );

            for ( let x = 0; x < arr.length; x++ )
            {
                if ( parseInt( arr[x].id ) === parseInt( window.location.href.split('/').pop() ) )
                {
                    setCallTo( arr[x] );
                }
            }

            socket.on(
                'allusers', ( arr ) => {

                    if ( arr.length > 1 )
                    {
                        const peer = new Peer(socket.id);
                        navigator.mediaDevices.getUserMedia(
                            { video: true, audio: true },
                            (stream) => {
                                const call = peer.call(arr[1], stream);
                                call.on("stream", (remoteStream) => {
                                    // Show stream in some <video> element.
                                    console.log( remoteStream )
                                });
                            },
                            (err) => {
                                console.error("Failed to get local stream", err);
                            },
                        );
                    }

                }
            )

        }, []
    )

    useEffect(
        () => {

            // let myVideoStream;

            const videoGrid = document.getElementById("Grid");
            const myVideo = document.createElement("video");
            myVideo.muted = true;
            myVideo.id = "myVideo";
            myVideo.style.width = '100%';
            // myVideo.style.height = '50%';
            navigator.mediaDevices.getUserMedia(
                {
                    audio: true,
                    video: true,
                }
            ).then(
                stream => {
                    // myVideoStream = stream;
                    addVideoStream(myVideo, stream);
                }
            );

            const addVideoStream = (video, stream) => {
                video.srcObject = stream;
                video.addEventListener(
                    "loadedmetadata", 
                    () => {
                        video.play();
                        setLoaded( true );
                        if ( $('#Grid').find('video').length > 0 )
                        {
                            return false;
                        }else
                        {
                            videoGrid.append(video);
                        }
                    }
                );
            };

        // eslint-disable-next-line react-hooks/exhaustive-deps
        }, []
    )

    // const TurnVideo = () => {

    //     const myVideo = document.querySelector("video");
        
    //     navigator.mediaDevices.getUserMedia(
    //         {
    //             video: !TurnVideoOff,
    //         }
    //     ).then(
    //         stream => {
    //             myVideo.srcObject = stream;
    //             setTurnVideoOff( !TurnVideoOff );
    //         }
    //     ).catch(
    //         err => {
    //             console.log( err );
    //         }
    //     );

    // }
    

    // const TurnAudio = () => {
        
    //     navigator.mediaDevices.getUserMedia(
    //         {
    //             audio: !TurnAudioOff
    //         }
    //     ).catch(
    //         err => {
    //             console.log( err );
    //         }
    //     );
    //     setTurnAudioOff( !TurnAudioOff );
    // }

    const VideoCall = () => {

        const myVideo = document.getElementById("myVideo");

        socket.emit(
            'getusers',
            socket.id
        );

    }
    
    return (
        <Container maxWidth="lg" className="OnLineVideoConsulting">

            <div className="Grid">
                <div id="Grid">
                    {
                        Loaded
                        ?
                        null
                        :
                        <div className="LoadingScreen">
                            <Typography variant="h5" sx={ { fontWeight: 'bold' } } component="div">
                                Loading Your Screen
                            </Typography>
                        </div>
                    }
                </div>
                <div className="CallingControls current">

                    <div className="ControlItem">
                        <Button 
                            variant="contained" 
                            sx={ 
                                    { 
                                        backgroundColor: '#4caf50', 
                                        color: '#FFFFFF', 
                                        mr: 1, 
                                        '&:hover': {
                                            backgroundColor: '#1b5e20'
                                        } 
                                    } 
                                }  
                            size="large"
                            onClick={ VideoCall }
                        >
                            <PhoneIcon />
                        </Button>
                        <Typography variant="subtitle1" component="div">
                            Call { CallTo.doctor_name }
                        </Typography>
                    </div>

                </div>
                
            </div>

        </Container>
    )

}

export default VideoConsulting;

