import React, { useEffect, useState } from 'react';
import './Appointments.css';

import { Link } from 'react-router-dom';

import Typography from '@mui/material/Typography';
import Avatar from '@mui/material/Avatar';
import Container from '@mui/material/Container';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';

const Appointments = () => {

    const [ AppointmentList, setAppointmentList ] = useState([]);

    useEffect(
        () => {

            setAppointmentList(
                [
                    {
                        id: 1,
                        doctor_name: 'Dr.Abdullah',
                        doctor_image: 'https://freesvg.org/img/Faceless-Male-Avatar-In-Suit.png',
                        doctor_gender: 'Male',
                        doctor_experience: 28,
                        appointment_time: '06:00 PM',
                        appointment_date: 'Thu 2 Feb 2022',
                        appointment_type: 'Video Consulting'
                    },
                    {
                        id: 2,
                        doctor_name: 'Dr.Fawad',
                        doctor_image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSbvEViMOriytRWM-Wu2DzCJAwGtVdEFx26RUmHxCb6XwZX_rCNvgQ4yYg7_2M_KeK74vQ&usqp=CAU',
                        doctor_gender: 'Male',
                        doctor_experience: 14,
                        appointment_time: '04:00 PM',
                        appointment_date: 'Mon 7 Feb 2022',
                        appointment_type: 'The Aga Khan University Hospital'
                    },
                    {
                        id: 3,
                        doctor_name: 'Dr.Shahzain',
                        doctor_image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSla-p7MqWVSLL2rpSQHlxEO6mKceKYPvZjo4oslefoeXE7-oMcRHP5IfT3qgllHC8kKvQ&usqp=CAU',
                        doctor_gender: 'Male',
                        doctor_experience: 10,
                        appointment_time: '01:00 PM',
                        appointment_date: 'Fri 6 Feb 2022',
                        appointment_type: 'Mamji Hospital'
                    },
                    {
                        id: 4,
                        doctor_name: 'Dr.Yousuf',
                        doctor_image: 'https://www.w3schools.com/w3images/avatar2.png',
                        doctor_gender: 'Male',
                        doctor_experience: 4,
                        appointment_time: '12:00 PM',
                        appointment_date: 'Sat 1 Feb 2022',
                        appointment_type: 'Memon Medical Institute Hospital'
                    }
                ]
            )

        }, []
    )

    return (
        <>
            <Container maxWidth="lg" className="AppointmentList">
            
                <Typography variant="h4" component="div" gutterBottom>
                    Appointments
                </Typography>

                <div className="AppointmentListGrid">

                    {
                        AppointmentList.map(
                            val => {

                                return <Appointment key={ val.id } data={ val } />

                            }
                        )
                    }

                </div>

            </Container>
        </>
    )

}

export default Appointments;

const Appointment = ( props ) => {

    const [AnchorEl, setAnchorEl] = React.useState( null );

    const open = Boolean(AnchorEl);

    const handleClick = ( e ) => {

        setAnchorEl(e.currentTarget);
    };
    const handleClose = () => {

        setAnchorEl(null);

    };


    return (
        <div className="AppointmentListGridItem">

            <div className="d-flex align-items-center">
                <Avatar alt="Doctor" src={ props.data.doctor_image } />

                <div className="pl-1">
                    <Typography variant="subtitle2" component="div">
                        { props.data.doctor_name }
                    </Typography>
                    <Typography variant="body2" component="div" color="#9e9e9e">
                        { props.data.doctor_experience } yrs, { props.data.doctor_gender }
                    </Typography>
                </div>

                <MoreVertIcon sx={ { ml: 'auto', cursor: 'pointer' } } onClick={ handleClick } />
                <Menu
                    id="basic-menu"
                    anchorEl={ AnchorEl }
                    open={ open }
                    onClose={ handleClose }
                    MenuListProps={
                        {
                            'aria-labelledby': 'basic-button',
                        }
                    }
                >
                    <MenuItem onClick={ handleClose }>Option 1</MenuItem>
                    <MenuItem onClick={ handleClose }>Option 2</MenuItem>
                    <MenuItem onClick={ handleClose }>Option 3</MenuItem>
                </Menu>
            </div>

            <div className="mt-1">
                <Typography variant="subtitle2" component="div">
                    { props.data.appointment_type }
                </Typography>
                <Typography variant="body2" component="div" color="#0091ea">
                    Appointment
                </Typography>
            </div>

            <div className="mt-1 d-flex align-items-center justify-content-between">
                
                <div>
                    <Typography variant="subtitle2" component="div">
                        { props.data.appointment_time }
                    </Typography>
                    <Typography variant="body2" component="div" color="#9e9e9e">
                        { props.data.appointment_date }
                    </Typography>
                </div>
                
                <div>
                    <Link to={ "/appointments/cancel/" + props.data.id }>
                        <Button 
                            variant="contained" 
                            sx={ 
                                { 
                                    backgroundColor: '#e53935', 
                                    color: '#FFFFFF', 
                                    mr: 1, 
                                    '&:hover': {
                                        backgroundColor: '#d32f2f'
                                    } 
                                } 
                            } 
                            size="small"
                        >
                            Cancel
                        </Button>
                    </Link>
                    <Link to={ "/appointments/start/" + props.data.id }>
                        <Button variant="contained" color="primary" size="small">
                            Start
                        </Button>
                    </Link>
                </div>

            </div>

        </div>
    )

}