import React, { useEffect, useState } from 'react';
import './BookAppointment.css';

import Container from '@mui/material/Container';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormLabel from '@mui/material/FormLabel';
import FormControl from '@mui/material/FormControl';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import ThumbUpOffAltIcon from '@mui/icons-material/ThumbUpOffAlt';

import Swal from 'sweetalert2';
import { useNavigate } from 'react-router-dom';

const BookAppointment = () => {

    let navigate = useNavigate();

    const [ Doctor, setDoctor ] = useState();
    const [ ComponentReady, setComponentReady ] = useState(false);
    const [ Data, setData ] = useState(
        {
            appointment: 'Video Consultation',
            date: '',
            time: ''
        }
    );

    useEffect(
        () => {

            if ( Data.appointment !== '' && Data.date !== '' && Data.time !== '' )
            {
                let html = 
                '<div style="display: flex; align-items: center; background: #f7f7f7; font-family: Arial;">' +
                    '<div>' +
                        '<img src=' + Doctor.image + ' alt="Doctor" width="120" height="120" style="border-radius: 50%" />' +
                    '</div>' +
                    '<div style="text-align: left; padding-left: 20px;">' + 
                        '<h3>' + Doctor.name + '</h3>' + 
                        '<p style="margin-bottom: 0">' + Data.appointment + '</p>' + 
                        '<div style="display: flex">' +
                            '<p>' + Data.time + ',</p> ' + 
                            '<p>' + Data.date + '</p>' + 
                        '</div>' +
                    '</div>' + 
                '</div>' +
                '<div style="font-family: Arial;">' +
                    '<strong>Do you want to confirm appointment booking?</strong>' +
                '</div>'
                
                Swal.fire(
                    {
                        html: html,
                        // icon: 'error',
                        confirmButtonText: 'Confirm'
                    }
                ).then(
                    res => {
                        if (res.isConfirmed) 
                        {
                            navigate("/appointments", { replace: true });
                        }
                    }
                )
            }

        // eslint-disable-next-line react-hooks/exhaustive-deps
        }, [Data.appointment, Data.date, Data.time]
    )

    useEffect(
        () => {

            let id = window.location.href.split('/').pop();
            let arr = [
                {
                    id: 1,
                    image: 'https://freesvg.org/img/Faceless-Male-Avatar-In-Suit.png',
                    name: 'Dr.Abdullah',
                    specialization: 'Andrologist, Endourologist, Sexologist, Urologist',
                    experience: 28,
                    reviews: 63,
                    fee: 1200,
                    available_places: [ "Video Consultation", "Audio Consultation" ],
                    available_dates: [ 'Mon 1 June 2022', 'Tue 2 June 2022', 'Wed 3 June 2022', 'Thu 4 June 2022', 'Fri 5 June 2022' ],
                    available_timings: [ '10:00:00', '12:00:00', '11:00:00', '09:00:00' ]
                },
                {
                    id: 2,
                    image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSbvEViMOriytRWM-Wu2DzCJAwGtVdEFx26RUmHxCb6XwZX_rCNvgQ4yYg7_2M_KeK74vQ&usqp=CAU',
                    name: 'Dr.Fawad',
                    specialization: 'Andrologist, Sexologist, Urologist',
                    experience: 14,
                    reviews: 55,
                    fee: 2000,
                    available_places: [ "Video Consultation", "Audio Consultation" ],
                    available_dates: [ 'Mon 1 June 2022', 'Tue 2 June 2022', 'Wed 3 June 2022', 'Thu 4 June 2022', 'Fri 5 June 2022' ],
                    available_timings: [ '10:00:00', '12:00:00', '11:00:00', '09:00:00' ]
                },
                {
                    id: 3,
                    image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSla-p7MqWVSLL2rpSQHlxEO6mKceKYPvZjo4oslefoeXE7-oMcRHP5IfT3qgllHC8kKvQ&usqp=CAU',
                    name: 'Dr.Yousuf',
                    specialization: 'Endourologist, Sexologist, Urologist',
                    experience: 45,
                    reviews: 88,
                    fee: 1500,
                    available_places: [ "Video Consultation", "Audio Consultation" ],
                    available_dates: [ 'Mon 1 June 2022', 'Tue 2 June 2022', 'Wed 3 June 2022', 'Thu 4 June 2022', 'Fri 5 June 2022' ],
                    available_timings: [ '10:00:00', '12:00:00', '11:00:00', '09:00:00' ]
                }
            ]

            let doctor;
            for ( let x = 0; x < arr.length; x++ )
            {
                if ( parseInt( arr[x].id ) === parseInt( id ) )
                {
                    doctor = arr[x];
                }
            }

            setDoctor( doctor );
            setComponentReady( true );

        }, []
    )

    const onChangeHandler = ( e ) => {

        const { name, value } = e.target;
        const val = {
            ...Data,
            [name]: value
        }

        setData( val );

    }

    return (
        <>
            {
                ComponentReady
                ?
                <Container maxWidth="lg" className="BookAppointmentContainer">

                    <div className="DoctorInfo ContainerItem">

                        <div>
                            <Avatar alt="Doctor" src={ Doctor.image } sx={{ width: 150, height: 150 }} />
                        </div>
                        <div className="d-flex align-items-center">
                            <div>
                                <Typography variant="h5" component="div" gutterBottom>
                                    { Doctor.name }
                                </Typography>

                                <Typography variant="subtitle1" component="div">
                                    { Doctor.specialization }
                                </Typography>

                                <Typography variant="subtitle1" component="div">
                                    { Doctor.experience } year(s) Experience
                                </Typography>

                                <Typography variant="subtitle2" component="span">
                                    Fee: Rs { Doctor.fee }
                                </Typography>
                            </div>
                        </div>

                    </div>

                    <div className="ContainerItem">

                        <FormControl>
                            <FormLabel id="demo-radio-buttons-group-label">Select Appointment Type</FormLabel>
                            <RadioGroup
                                aria-labelledby="demo-radio-buttons-group-label"
                                defaultValue="Video Consultation"
                                name="appointment"
                                value={ Data.appointment }
                                onChange={ onChangeHandler }
                                row
                            >
                                {
                                    Doctor.available_places.map(
                                        ( val, index ) => {

                                            return <FormControlLabel key={ index } value={ val } control={<Radio />} label={ val } />;

                                        }
                                    )
                                }
                            </RadioGroup>
                        </FormControl>

                    </div>

                    <div className="ContainerItem">

                        <FormControl>
                            <FormLabel id="demo-radio-buttons-group-label">Select Date</FormLabel>
                            <RadioGroup
                                aria-labelledby="demo-radio-buttons-group-label"
                                defaultValue={ new Date().toDateString() }
                                name="date"
                                value={ Data.date }
                                onChange={ onChangeHandler }
                                row
                            >
                                <FormControlLabel value={ new Date().toDateString() } control={<Radio />} label={ new Date().toDateString() } />
                                {
                                    Doctor.available_dates.map(
                                        ( val, index ) => {

                                            return <FormControlLabel key={ index } value={ val } control={<Radio />} label={ val } />;

                                        }
                                    )
                                }
                            </RadioGroup>
                        </FormControl>

                    </div>

                    <div className="ContainerItem">

                        <FormControl>
                            <FormLabel id="demo-radio-buttons-group-label">Select Time Slot</FormLabel>
                            <RadioGroup
                                aria-labelledby="demo-radio-buttons-group-label"
                                defaultValue={ new Date().toTimeString().substring(0,8) }
                                name="time"
                                value={ Data.time }
                                onChange={ onChangeHandler }
                                row
                            >
                                <FormControlLabel value={ new Date().toTimeString().substring(0,8) } control={<Radio />} label={ new Date().toTimeString().substring(0,8) } />
                                {
                                    Doctor.available_timings.map(
                                        ( val, index ) => {

                                            return <FormControlLabel key={ index } value={ val } control={<Radio />} label={ val } />;

                                        }
                                    )
                                }
                            </RadioGroup>
                        </FormControl>

                    </div>

                    <div className="ContainerItem">

                        <Box
                            component="div"
                            sx={
                                {
                                    p: 2,
                                    backgroundColor: '#F3F5F9'
                                }
                            }
                        >
                            <Typography variant="subtitle1" component="div" color="#014E78">
                                Reviews For { Doctor.name }
                            </Typography>

                            <Typography variant="body2" component="span">
                                These are the views of the patients and may not necessarily reflect the doctor's medical expertise.
                            </Typography>
                        </Box>

                        <Container maxWidth="md" className="">
                            <div className="d-flex pt-2">

                                <ThumbUpOffAltIcon fontSize="large" />
                                <div className="pl-2">

                                    <Typography variant="subtitle1" component="div">
                                        <b>Atif</b>
                                    </Typography>

                                    <Typography variant="body2" component="span">
                                        Online consultation session with dr fawad sb remaind highly beneficial and fruitful.in current days of lockdown,it was indeed a blessing. Live long dr fawad sb. Usman sargodha
                                    </Typography>

                                </div>

                            </div>
                        </Container>

                        <br />

                        <Typography variant="body2" component="div" className="text-center" color="primary">
                            <u>View All</u>
                        </Typography>

                    </div>

                </Container>
                :
                null
            }
        </>
    )

}

export default BookAppointment;